import React, { useEffect, useState } from "react";
import axios from "axios";
import { Box, List, ListItem, ListItemText } from "@mui/material";
import "./index.css";

const Dashboard = () => {
  const [names, setNames] = useState([]);
  const [selectedImage, setSelectedImage] = useState(null); //   URL

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await axios.get("http://localhost:8080/dashboard/items");
      const data = response.data;
      const nameList = data.map((item) => item.name);
      setNames(nameList);
    } catch (error) {
      console.error(error);
    }
  };

  const handleImageClick = async (name) => {
    try {
      const response = await axios.get(
        "http://localhost:8080/dashboard/images"
      );
      const data = response.data;
      const imageUrl = data.find((item) => item.name === name)?.imageUrl;
      setSelectedImage(imageUrl);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div>
      <h1></h1>
      <div className="scrollableList">
        <List>
          {names.map((name, index) => (
            <ListItem key={index} onClick={() => handleImageClick(name)}>
              <ListItemText primary={name} />
            </ListItem>
          ))}
        </List>
      </div>
      {selectedImage && (
        <Box mt={2}>
          <img src={selectedImage} alt="Selected Image" />
        </Box>
      )}
    </div>
  );
};

export default Dashboard;
