import React, { useContext } from "react";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./index.css";
import axios from "axios";
import bgImage from "src/assets/images/wave-blue-1.png";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { IconButton, Button, TextField } from "@mui/material";
import { AuthContext } from "src/contexts/AuthContext";
import { Visibility, VisibilityOff } from "@mui/icons-material";

function handleLogout() {
  //   
  //    .
}

const ProfilePage = () => {
  //  
  const { id, password } = useContext(AuthContext);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [adminId, setIsAdmin] = useState("");

  const movePage = useNavigate();
  const [isHovered, setIsHovered] = useState(false);

  const handleClick = () => {
    movePage("/dashboard/main-page/");
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`/dashboard/profile/${id}`);
        const { data } = response;

        console.log(data); //  response 

        // name, phone, email, adminId   
        const { name, phone, email, adminId } = data;

        setName(name);
        setEmail(email);
        setPhone(phone);
        setIsAdmin(adminId);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, [id]);

  const [showFullNumber, setShowFullNumber] = useState(false);
  const handleToggleVisibility = () => {
    setShowFullNumber(!showFullNumber);
  };

  const [showEditForm, setShowEdit] = useState(true);

  const profileEditToggle = () => {
    setShowEdit(!showEditForm);
  };

  return (
    <div className="set-maxheight">
      <div>
        <div className="icon-container">
          <IconButton
            aria-label="back"
            sx={{ color: "white" }}
            onClick={handleClick}
          >
            <ArrowBackIcon />
          </IconButton>
        </div>
        <div className="menu-selector">
          <Button
            variant="outlined"
            size="small"
            sx={{
              color: "white",
              borderColor: "white",
              margin: "0 10px",
              "&:hover": {
                borderColor: "white",
                backgroundColor: "rgba(255, 255, 255, 0.5)",
              },
            }}
            onClick={handleLogout}
          >
            logout
          </Button>
        </div>
      </div>
      <div>
        <div className="background-hero"></div>
        <div className="image-container">
          <img
            className="background-img disable-select"
            src={bgImage}
            alt="Background Image"
          ></img>
        </div>
      </div>
      <div className="set-top">
        <div className="title-container">
          <div className="name-id-text">
            <h1>{name}</h1>
            <h2> !</h2>
          </div>
        </div>
        {showEditForm ? (
          <div className="top profile-container">
            <div className="typo-container">
              <div className="info-text">
                <p className="font-title"></p>
                <p className="font-body">{id}</p>
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"></p>
                <p className="font-body">{email}</p>
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"></p>
                <p className="font-body">
                  {phone.slice(0, 3)}-{phone.slice(3, 7)}-{phone.slice(7)}
                </p>
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"> </p>
                <p className="font-body">
                  {showFullNumber
                    ? adminId
                    : adminId.substr(0, 4) +
                      (adminId.length > 4
                        ? "*".repeat(adminId.length - 4)
                        : "no admin id")}
                </p>

                <span
                  style={{ float: "right", cursor: "pointer", color: "gray" }}
                  onClick={handleToggleVisibility}
                >
                  {showFullNumber ? <VisibilityOff /> : <Visibility />}
                </span>
              </div>
              <div className="divider"></div>
            </div>
            <div className="set-to-center set-to-bottom">
              <Button variant="outlined" onClick={profileEditToggle}>
                 
              </Button>
            </div>
          </div>
        ) : (
          <div className="top edit-container">
            <div className="typo-container">
              <div className="info-text">
                <p className="font-title"></p>
                <TextField
                  variant="standard"
                  size="small"
                  defaultValue={id}
                  fullWidth
                  disabled
                />
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"></p>
                <TextField
                  variant="standard"
                  size="small"
                  fullWidth
                  type="password"
                />
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"></p>
                <TextField
                  variant="standard"
                  size="small"
                  defaultValue={email}
                  fullWidth
                />
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"></p>
                <TextField
                  variant="standard"
                  size="small"
                  defaultValue={phone}
                  fullWidth
                />
              </div>
              <div className="divider"></div>
              <div className="info-text">
                <p className="font-title"> </p>
                <TextField
                  variant="standard"
                  size="small"
                  defaultValue={adminId}
                  fullWidth
                  disabled
                />
              </div>
              <div className="divider"></div>
            </div>
            <div className="set-to-center set-to-bottom">
              <Button
                variant="outlined"
                size="small"
                sx={{ margin: "0 1vw 0 1vw" }}
              >
                 
              </Button>
              <Button
                variant="outlined"
                onClick={profileEditToggle}
                size="small"
                sx={{ margin: "0 1vw 0 1vw" }}
              >
                 
              </Button>
              <Button
                variant="outlined"
                color="error"
                size="small"
                onClick={profileEditToggle}
                sx={{ margin: "0 1vw 0 1vw" }}
              >
                
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProfilePage;
